@import url("https://fonts.googleapis.com/css?family=Merienda");
* {
  font-family: "Merienda", cursive; }

html,
body {
  margin: 0;
  height: 100%; }

body {
  background: #a8bc9a;
  background-image: url("../Images/recipe-background.jpg");
  background-size: cover; }

input[type="text"],
textarea {
  border: 1px solid #cccccc !important; }

.wrapper-all {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  height: 100vh; }
  .wrapper-all .inner-all {
    -ms-flex: 1 0 auto;
        flex: 1 0 auto; }

.site-header {
  text-align: center;
  color: white; }

.recipeContainer {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin: 0 auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  width: 90%; }

.btn-addRecipe {
  display: block;
  margin: 0 auto 10px;
  padding: 15px;
  color: white;
  background-color: #376e4b;
  border: 3px double #ffffff;
  text-transform: uppercase;
  font-size: 12px;
  font-weight: bold;
  outline: none;
  -webkit-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
  margin-top: 15px; }
  .btn-addRecipe:hover {
    background-image: url("../Images/add.png");
    background-position: center;
    background-repeat: no-repeat;
    color: transparent;
    cursor: pointer; }



